import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';

import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';






const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    height: 100,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  boton: {
    color: 'white',
    fontSize: '1.2em',
    margin: '0 0.6em', 
    "&:hover": {
      backgroundColor: '#C25776'
    }
  },
  linkMenu: {
    color:'white',
    textDecoration: 'none',
    fontSize: '1.3em'
  },
  login: {
    
    padding: '0.1em 1em',
    fontSize: '1.3em',
    backgroundColor: '#574555'
  }

  
  
}));

export default function ButtonAppBar({Link}) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <AppBar position="static">
        
        <Toolbar >
          <Grid container  justify='center' direction='row'>
            
          
          <Button variant="text" color="default" className={classes.boton}><Link className={classes.linkMenu} to="/">HOME</Link></Button>
          <Button variant="text" color="default" className={classes.boton}><Link className={classes.linkMenu} to="/tienda">TIENDA</Link></Button>
          <Button variant="text" color="default" className={classes.boton}><Link className={classes.linkMenu} to="/contacto">CONTACTO</Link></Button>
          <Button variant="text" color="default" className={classes.boton}><Link className={classes.linkMenu} to="/micuenta">MI CUENTA</Link></Button>
          <Button  variant="text" color="default" className={classes.boton}><Link className={classes.linkMenu} to="/registro">REGISTRARSE</Link></Button>
          

          </Grid>

          <Button className={classes.login} variant="text"><Link className={classes.linkMenu} to="/login">Login</Link></Button>
        </Toolbar>
      </AppBar>
    </div>
  );
}